svg {
  /* width:100%; */
  /* min-height: calc(100vh); */
  width: 100%; 
  display:block;
  position:absolute;
  top:60px;
  left:0;
  z-index: -1;
  margin-bottom: -60px;
}

.bottom-hexa {
  position: relative;
  height: 35vw;
  margin-top: -38vw;
  z-index: -2;
}

.top-hexa {
  max-height: 800px;
}

@media print {
  .bottom-hexa {
    visibility: hidden;
  }
  .top-hexa {
    opacity: 0.25;
    top: -10px;
    height: 45vw;
  }
}